.splash-card-container {
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.splash-card-container h1 {
  font-weight: 300;
  font-size: 2.4rem;
  margin: 0; 
}

.splash-card-container h2 {
  font-weight: 600;
  font-size: 1.4rem;
  margin: 0;
}

.splash-card-container hr {
  margin: .5rem 0;
  width: 100%;
  border-color: white;
}

.splash-img-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 169px;
}

.splash-img-container img {
  height: 160px;
  width: 160px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 33%;
     object-position: 50% 33%;
  border-radius: 50%;
}

.splash-img-container img:hover {
  cursor: pointer;
  width: 168px;
  height: 168px;
  border: 1px solid #3f51b5;
  border-radius: 50%; 
  -webkit-transition: all .25s ease; 
  -o-transition: all .25s ease; 
  transition: all .25s ease;
}

.splash-card-container ul {
  font-size: 1.2rem;
  list-style-type: none;
  padding: 0;
  margin: 1rem 0 0 0;
}

.splash-card-container ul li {
  display: inline-block;
  margin: 0 .5rem;
  line-height: 1.3rem;
}

.splash-card-container ul li a {
  color: white;
}

.splash-card-container ul li a:hover {
  font-size: 1.3rem;
  color: #e3f2fd;
}


