#portfolio {
  background-color: white;
  padding: 15px;
}
.show-ml-c {
  margin: 1.5rem 0 0;
}
.show-ml-c span {
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 1rem;
  padding: 0 5px;
  border-radius: 4px;
  border: 1px solid #ddd;
}
.show-ml-c span:hover {
  cursor: pointer;
  background-color: #eceff1;
}

.proj-item-img-c {
  position: relative;
  overflow: hidden;
  -o-object-fit: cover;
     object-fit: cover;
}
.proj-item-img-c img {
  width: 100%;
  height: 100%;
}
.proj-item-img-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 15px;
  background-color: #00000075;
  color: white;
  font-weight: 600;
  opacity: 0;
  -webkit-transition: opacity .8s;
  -o-transition: opacity .8s;
  transition: opacity .8s;
}
.proj-item-img-overlay:hover {
  cursor: default;
  opacity: 1;
  -webkit-transition: opacity .8s;
  -o-transition: opacity .8s;
  transition: opacity .8s;
}
.proj-item-img-overlay p {
  font-family: "Merriweather Sans", "Helvetica", "Arial", sans-serif;
  font-size: .9rem;
  line-height: 1.5;
}
.proj-item-info {
  padding: 8px 16px;
}
.proj-item-info:hover {
  cursor: default;
}
.proj-item-name {
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 1.2rem;
  font-weight: 300;
  margin: 0;
}
.proj-item-desc {
  font-family: "Merriweather Sans", "Helvetica", "Arial", sans-serif;
  font-size: .9rem;
  font-style: italic;
  line-height: 1.5;
  margin: 10px 0 0;
}
.proj-item-links {
  display: -ms-flexbox;
  display: flex;
  padding: 8px 16px;
}
.proj-item-links a {
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 1rem;
  color: #3d6bb3;
  margin: auto;
  text-decoration: none;
  padding: 0 5px;
  border-radius: 4px;
}
.proj-item-links a:hover {
  color: #0d47a1;
  background-color: #f0f8ff;
}


