#skills {
  background-color: #0d47a1;
  color: white;
  padding: 15px;
  background-image: url("../../../../../images/splash.jpg");
  /* background-image: url("../../../../../images/splash24.gif"); */
  background-position: center;
  background-size: cover;
}

