#skills hr {
  border-color: white;
}
#skills h3 {
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.1;
  margin: 0 0 1rem;
  color: #c5cae9;
}

#skills ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
#skills ul li {
  font-size: 1.1rem;
  line-height: 1.5;
}
#skills ul li:hover {
  color: #e3f2fd;
}

.icon {
  height: 7rem;
  padding: .6rem;
  -webkit-filter: brightness(0) invert(0.6);
          filter: brightness(0) invert(0.6); }
.icon:hover {
    -webkit-filter: brightness(1) invert(0);
            filter: brightness(1) invert(0); }

@media (max-width: 1000px) {
  .icon {
    height: 6rem;
  }
}
